<template>
    <div class="container table-container">
        <div class="row">
            <div class="col-12">
                <div ref="chartRef3" style="height: 400px;"></div>
            </div>
        </div>
        <div class="row">
            <div class="col-6">
                <div ref="chartRef1" style="height: 400px;"></div>
            </div>
            <div class="col-6">
                <div ref="chartRef2" style="height: 400px;"></div>
            </div>
        </div>
    </div>
</template>

<script>
import { onMounted, ref } from 'vue';
import * as echarts from 'echarts';
import $ from 'jquery'
export default {
  name: 'OverView',
  components: {
  },
  setup() {
    const chartRef1 = ref(null);
    const chartRef2 = ref(null); 
    const chartRef3 = ref(null); 
    onMounted(() => {
      // 
      const chartDom1 = chartRef1.value;
      const myChart1 = echarts.init(chartDom1);
      const option1 = {
        title: {
          text: ''
        },
        tooltip: {},
        xAxis: {
          data: ['', '', '', '', '', '', '', '', '', '']
        },
        yAxis: {},
        series: [
          {
            name: '',
            type: 'bar',
            data: [2033, 1030, 506, 371, 253, 251, 179, 172, 126, 110]
          }
        ]
      };
      myChart1.setOption(option1);

      // 
      const chartDom2 = chartRef2.value;
      const myChart2 = echarts.init(chartDom2);
      const option2 = {
        title: {
          text: '',
          left: 'center'
        },
        tooltip: {
          trigger: 'item'
        },
        legend: {
          orient: 'vertical',
          left: 'left'
        },
        series: [
          {
            name: '',
            type: 'pie',
            radius: '50%',
            data: [
              { value: 5, name: '' },
              { value: 6, name: '' },
              { value: 9, name: '' },
              { value: 8, name: '' },
              { value: 10, name: '' },
              { value: 24, name: '' },

            ],
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }
        ]
      };
      myChart2.setOption(option2);

      let dates = ref([])
      let ip_address = ref([])
      let visits = ref([])

      $.ajax({
        url:'http://127.0.0.1:8000/overview/count/',
        type:'get',
        success(resp){
          // 
          let entries = Object.entries(resp);
          // 
          entries.reverse();
          // 
          let reversedResp = Object.fromEntries(entries);
          console.log('resp.reverse()',reversedResp)
          dates.value = Object.keys(reversedResp)
          ip_address.value = Object.values(reversedResp).map(dateData => dateData.ip_address);
          visits.value = Object.values(reversedResp).map(dateData => dateData.total_requests);
          const chartDom3 = chartRef3.value;
          const myChart3 = echarts.init(chartDom3);
          const option3 = {
            title: {
              text: '',
            },
            tooltip: {
                trigger: 'axis', //  'item'  'axis'
            },
            xAxis: {
                data: dates.value
            },
            yAxis: {},
            series: [
                {
                name: 'IP', // 
                data: ip_address.value,
                type: 'line',
                stack: 'x',
                itemStyle: {
                    color: 'red' 
                }
                },
                {
                name: 'Visit', // 
                data: visits.value,
                type: 'line',
                stack: 'x',
                itemStyle: {
                    color: 'black' 
                }
                }
            ]
            };
            myChart3.setOption(option3);
            },
      })

    });

    return {
      chartRef1,
      chartRef2, // 
      chartRef3
    };
  },
};
</script>

<style scoped>
  .table-container { /*  */
    width: 100%;
    height: 580px; /*  */
    overflow-y: auto; /*  */
    /* margin-bottom: 20px;  */
  }
</style>