import { createRouter, createWebHashHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import UserView from '../views/UserView.vue' 
import ThingView from '../views/ThingView.vue'
import CommentView from '../views/CommentView.vue'
import ClassificationView from '../views/ClassificationView.vue'
import TagView from '../views/TagView.vue'
import LoginView from '../views/LoginView.vue'
import LogView from '../views/LoginLogView.vue'
import Overview from '../views/Overview.vue'
import store from '../store';
const isUserLoggedIn = () => {
  if(store.state.user.is_login)
    return true
  else 
    return false
}

const routes = [
  {
    path: '/',
    name: 'home', // home
    component: HomeView, // 
  },
  {
    path: '/login',
    name: 'login',
    component: LoginView,
  },
  {
    path: '/admin',
    component: HomeView, // HomeViewadmin
    children: [
      {
        path: 'comment', // /
        name: 'comment',
        component: CommentView,
        meta: { requiresAuth: true },
      },
      {
        path: 'thing', // /
        name: 'thing',
        component: ThingView,
        meta: { requiresAuth: true },
      },
      {
        path: 'classification', // /
        name: 'classification',
        component: ClassificationView,
        meta: { requiresAuth: true },

      },
  //     {
  //       path: '', // 
  //       name: 'admin-default', // 
  //       redirect: 'thing', // thing
  //       meta: { requiresAuth: true },
  //     },      
      {
        path: 'user/', 
        name: 'user',
        component: UserView,
        meta: { requiresAuth: true },
      },
      {
        path: 'loginlog/', 
        name: 'loginlog',
        component: LogView,
        meta: { requiresAuth: true },
      },      
      {
        path: 'overview/', 
        name: 'overview',
        component: Overview,
        meta: { requiresAuth: true },
      },
      {
        path: 'tag/', 
        name: 'tag',
        component: TagView,
        meta: { requiresAuth: true },
      },
    ]
  },

]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})
router.beforeEach((to, from, next) => {
  // 
  if (to.matched.some(record => record.meta.requiresAuth)) {
    // 
    if (!isUserLoggedIn()) {
      // 
      next({
        path: '/login',
        query: { redirect: to.fullPath } // 
      })
    } else {
      // 
      next()
    }
  } else {
    // 
    next()
  }
})
export default router