<template>
  <div class="container table-container">
    <div class="row">
      <div class="d-flex justify-content-end"> <!--  d-flex  justify-content-end  -->
        <button type="button" class="btn btn-primary" data-bs-toggle="modal" data-bs-target="#staticBackdrop" @click="toAddUser"></button>
        <!-- @click="deleteUser" -->
        <button type="button" class="btn btn-danger" @click="getids"></button>
        <form class="d-flex" role="search">
          <input class="form-control me-2" type="search" v-model="searchData"  placeholder="" aria-label="Search">
          <button class="btn btn-outline-success" @click="search">
            <i class="bi bi-search"></i>
          </button>
       </form>
      </div>
    </div>
      <div class="row"> 
      <table class="table table-striped table-hover">
          <thead>
              <tr>
                <th ><input type="checkbox" id="select-all"  v-model="selectAll" @change="toggleSelectAll"></th> <!--  -->
                <th v-for="(header, key) in headerMappingTableHeaders" :key="key">{{ header }}</th>
              </tr>
          </thead>
          <tbody>
            <tr v-for="(responseData,index) in responseDatas" :key="responseData.id">
              <td><input type="checkbox" class="row-select" v-model="selectedIds" :value="index + 1 + (currentPage - 1) * 10"></td> <!--  -->
              <td>{{ index + 1 + (currentPage - 1) * 10}}</td>
              <td v-for="(header, key) in filteredHeaders" :key="key">{{ responseData[header] }}</td>
              <td >
                  <button type="button" class="btn" data-bs-toggle="modal" data-bs-target="#staticBackdrop" @click="updateUser(responseData.id)"></button>
                  
                  <button type="button" class="btn" @click="deleteUser(responseData.id)"></button>
              </td>
            </tr>
          </tbody>
              
        </table>
    </div>
  </div>
  <!--  -->
  <nav aria-label="Page navigation example">
    <ul class="pagination">
      <li class="page-item">
        <a v-if="currentPage == 1" class="page-link disabled" href="#" aria-label="Previous" @click.prevent="goPrevious">
          <span aria-hidden="true">&laquo;</span>
        </a>
        <a v-else class="page-link" href="#" aria-label="Previous" @click.prevent="goPrevious">
          <span aria-hidden="true">&laquo;</span>
        </a>
      </li>
      <li class="page-item" v-for="page in totalPages" :key="page">
          <a class="page-link" href="#" @click.prevent="goToPage(page)">{{ page }}</a>
      </li>
      <li class="page-item">
        <a v-if="currentPage == totalPages" class="page-link disabled" href="#" aria-label="Next" @click.prevent="goNext">
          <span aria-hidden="true">&raquo;</span>
        </a>
        <a v-else class="page-link" href="#" aria-label="Next" @click.prevent="goNext">
          <span aria-hidden="true">&raquo;</span>
        </a>
      </li>
    </ul>
  </nav>
  <!-- Modal -->
  <div class="modal fade" id="staticBackdrop" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1" aria-labelledby="staticBackdropLabel" aria-hidden="true">
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <h1 v-if="isAddUser" class="modal-title fs-5" id="staticBackdropLabel"></h1>
          <h1 v-else class="modal-title fs-5" id="staticBackdropLabel"></h1>

          <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close" @click="editUser"></button>
        </div>
        <div class="modal-body">
          <form>
            <div class="row mb-3" v-if="isAddUser">
              <div class="col-2">
                <label  class="form-label">:</label>
              </div>
              <div class="col-10">
                <input  type="text" class="form-control" placeholder="" v-model="userdata.username" >
              </div>
            </div>
            <div class="row mb-3" v-if="isAddUser">
              <div class="col-2">
                <label  class="form-label">:</label>
              </div>
              <div class="col-10">
                <input  type="password" class="form-control" placeholder="" v-model="userdata.password" >
              </div>
            </div>
            <div class="row mb-3">
              <div class="col-2">
                <label class="form-label">:</label>
              </div>
              <div class="col-10" >
                <input type="text" class="form-control" placeholder="" v-model="userdata.nickname">
              </div>
            </div>
            <div class="row mb-3">
              <div class="col-2">
                <label class="form-label">:</label>
              </div>
              <div class="col-10" >
                <select class="form-select" placeholder="123" v-model="userdata.gender">
                  <option value=""></option>
                  <option value=""></option>
                </select>              
              </div>
            </div>
            <div class="row mb-3">
              <div class="col-2">
                <label class="form-label">:</label>
              </div>
              <div class="col-10" >
                <input type="text" class="form-control" placeholder="" v-model="userdata.age">
              </div>
            </div>
            <div class="row mb-3">
              <div class="col-2">
                <label class="form-label">:</label>
              </div>
              <div class="col-10">
                <select class="form-select" placeholder="123" v-model="userdata.eq">
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                </select>
              </div>
            </div>
            <div class="row mb-3">
              <div class="col-2">
                <label class="form-label">:</label>
              </div>
              <div class="col-10" >
                <input type="text" class="form-control" placeholder="" v-model="userdata.hprice">
              </div>
            </div>
            <div class="row mb-3">
              <div class="col-2">
                <label class="form-label">:</label>
              </div>
              <div class="col-10">
                <select class="form-select"  v-model="userdata.status">
                  <option value=""></option>
                  <option value=""></option>
                </select>
              </div>
            </div>
            <div class="row mb-3">
              <div class="col-2">
                <label class="form-label">:</label>
              </div>
              <div class="col-10">
                <input type="text" class="form-control" placeholder="" v-model="userdata.email">
              </div>
            </div>
            <div class="row mb-3">
              <div class="col-2">
                <label class="form-label">:</label>
              </div>
              <div class="col-10">
                <input type="text" class="form-control" placeholder="" v-model="userdata.phonenumber">
              </div>
            </div>
            <div class="row mb-3">
              <div class="col-2">
                <label class="form-label">:</label>
              </div>
              <div class="col-10">
                <textarea class="form-control" id="remark" rows="3" v-model="userdata.remark"></textarea>
              </div>
            </div>
            </form>
        </div>
        <div v-if="isAddUser" class="error-message">{{ error_message }}</div>
        <div class="modal-footer">
          <button type="button" class="btn btn-secondary" data-bs-dismiss="modal" @click="resetEditId"></button>
          <button v-if="isAddUser" type="button" class="btn btn-primary"  id="add_user_confirm_btn" @click="addUser"></button>
          <button v-else type="button" class="btn btn-primary"  data-bs-dismiss="modal" @click="updateUserInfo(editId)"></button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from'jquery'
import {reactive ,ref,computed } from 'vue'
import { useStore } from 'vuex';
export default {
  name: 'UserView',
  setup() {
      let store = useStore()
      const responseDatas = ref([])
      const totalPages = ref(0) 
      const selectAll = ref(false); // 
      const currentPage = ref(1)  //
      const selectedIds = ref([]); // ID
      const userdata = reactive({
        username:'',
        password:'',
        nickname:'',
        province:'',
        status:'',
        email:'',
        phonenumber:'',
        eq:'',
        remark:'',
        hprice:'',
      })
      const isAddUser = ref(false); // 
      const editId = ref(0) //id
      const error_message = ref('')
      const searchData = ref('')  //

      const headerMappingTableHeaders = {
        'id':'',
        'nickname':'',
        'phonenumber':'',
        'age':'',
        'gender':'',
        'province':'',
        'remark':'',
        'status':'',
        'create_time':'',
        'operate':'',
      }

      const updateUserList = () => {
        $.ajax({
          url: "http://47.109.76.15:8000/teacher/teacherlist",
          type: "GET",
          data:{
            index:currentPage.value
          },
          success(resp){
            responseDatas.value = resp
            totalPages.value = Math.ceil(resp[0].count / 10)
          }      
      })
      }
      
      const filteredHeaders = computed(() => {
        return Object.keys(headerMappingTableHeaders)
          .filter(key => key !== 'id' && key !== 'operate');
      });
      

      const goPrevious = () => {
        if (currentPage.value > 1) {
          currentPage.value = currentPage.value - 1
          updateUserList();
        }
      }
      const goToPage = (i) =>{
        currentPage.value = i
        updateUserList()
      }
      const goNext = () => {
        if (currentPage.value < totalPages.value) {
          currentPage.value = currentPage.value + 1
          updateUserList();
        }
      }
      //userdata
      const updateUserData = (data) => {
        userdata.username = data.username
        userdata.nickname = data.nickname
        userdata.role= data.role
        userdata.status= data.status
        userdata.email= data.email
        userdata.eq = data.eq
        userdata.phonenumber= data.phonenumber
        userdata.province= data.province
        userdata.remark= data.remark
        userdata.hprice= data.hprice
        userdata.age= data.age
        userdata.gender= data.gender
      }
      //userdata
      const updateUser = (userId) => {
        editId.value = userId
        $.ajax({
            url:'http://47.109.76.15:8000/teacher/teacherinfo/',
            type:'get',
            data:{
              'user_id':userId
            },
            success(resp){
              updateUserData(resp)
              console.log(resp)
            },
          })
      }
      const updateUserInfo = (userId) =>{
        console.log('updateUserInfo------',userdata)
        $.ajax({
            url:'http://47.109.76.15:8000/teacher/teacherinfo/',
            type:'post',
            data:{
              'user_id':userId,
              'nickname':userdata.nickname,
              'status':userdata.status === ''?0:1,
              'email':userdata.email,
              'phonenumber':userdata.phonenumber,
              'hprice':userdata.hprice,
              'eq':userdata.eq === '' ? 2:userdata.eq === ''?3:4,
              'gender':userdata.userdata,
              'userdata':userdata.age,
              'remark':userdata.remark,
            },
            headers: {
              'Authorization': "Bearer " + store.state.user.access,
            },
            success(resp){
              updateUserList()
              console.log('updateUserInfo----',resp)
            },
          })
      }
      //
      const addUser = () =>{
        error_message.value = ''
        const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
        if(userdata.username == '' && userdata.password == ''){
          error_message.value = "";
        }else if(userdata.email != '' && !emailRegex.test(userdata.email)){
          error_message.value = "";
        }
        else{
          $.ajax({
          url:'http://127.0.0.1:8000/teacher/register/',
          type:'post',
          data:{
              'username':userdata.username,
              'password':userdata.password,
              'password_confirm':userdata.password,
              'nickname':userdata.nickname,
              'status':userdata.status === ''?0:1,
              'email':userdata.email,
              'phonenumber':userdata.phonenumber
          },
          headers: {
            'Authorization': "Bearer " + store.state.user.access,
          },
          success(resp){
              console.log(resp)
              $('#staticBackdrop').hide()
              updateUserList()
          }
        })
        }
      }
      //
      const editUser = () =>{
        isAddUser.value = false
      }
      const toAddUser = () => {
        editId.value = 0
        userdata.username = ''
        userdata.nickname = ''
        userdata.password = ''
        userdata.gender= ''
        userdata.status= ''
        userdata.hprice = ''
        userdata.remark = ''
        userdata.age = ''
        userdata.eq = ''
        userdata.email = ''
        userdata.phonenumber = ''
        isAddUser.value = true
      }
      const deleteUser = (index) =>{
        $.ajax({
          url: "http://47.109.76.15:8000/teacher/teacherinfo/",
          type: "delete",
          data:{
            'user_id': index
          },
          headers:{
            'Authorization': "Bearer " + store.state.user.access,
          },
          success(resp){
            console.log('delete',resp)
            updateUserList()
          }
        })
      }
      //
      const search = () =>{
        console.log(searchData.value,'-------searchData.value')
        currentPage.value = 1
        if(searchData.value == ''){
          updateUserList()
        }else{
          $.ajax({
            url: "http://47.109.76.15:8000/teacher/search/",
            type:'GET',
            data:{
              data:searchData.value,
              index:currentPage.value
            },
            success(resp){
              console.log('search : ',searchData.value," success")
              responseDatas.value = resp
              totalPages.value = Math.ceil(resp[0].count / 10)
            }
          })
        }

      }
      updateUserList()
      return {
        totalPages,
        headerMappingTableHeaders,
        responseDatas,
        filteredHeaders,
        selectAll,
        // toggleSelectAll,
        selectedIds,
        goPrevious,
        goToPage,
        goNext,
        currentPage,
        userdata,
        updateUser,
        isAddUser,
        addUser,
        editUser,
        editId,
        updateUserInfo,
        error_message,
        toAddUser,
        deleteUser,
        search,
        searchData
      };
  },
};
</script>

<style scoped>

.table-container { /*  */
  width: 100%;
  height: 580px; 
  overflow-y: auto; 
}

.btn{
  margin-right: 20px;
}
.pagination{
  float: right;
  margin-right: 10px;
}
.error-message {
  margin: auto;
  color: red;
}
.disabled {
  pointer-events: none; /*  */
  opacity: 0.65;        /*  */
  cursor: not-allowed;  /*  */
}
th{
  font-size:16px;
  text-align: center; /*  */
}
td{
  font-size:14px;
  text-align: center; /*  */
}
</style>