<template>
  <div class="container">
    <ul class="list-group">
    <li
      class="list-group-item"
      v-for="item in menuItems"
      :key="item.name"
      :class="{ active: selectedItem === item.name }"
      @click="selectItem(item)"
    >
      <i :class="item.img"></i>
      {{ item.display }}
    </li>
  </ul>
  </div>
  
</template>
  
  <script>
    import router from '@/router/index';
    import {ref} from 'vue'
  export default {
    name: 'AlisdView',
    setup() {
        const selectedItem = ref('');
        const menuItems = [
            { name: '', display: '', route: "/admin/thing" ,img:"bi bi-card-list"},
            { name: '', display: '', route: "/admin/classification",img:"bi bi-columns"},
            { name: '', display: '', route: "/admin/tag",img:"bi bi-tag"},
            { name: '', display: '', route: "/admin/comment",img:"bi bi-chat-dots"},
            { name: '', display: '', route: "/admin/user",img:"bi bi-person"},
            { name: '', display: '', route: "/admin/thing",img:"bi bi-folder"},
            // { name: '', display: ''},
            { name: '', display: '', route: "/admin/loginlog",img:"bi bi-border-all"},
            // { name: '', display: '', route: "/admin/oplog",img:"bi bi-border-all" },
            // { name: '', display: '', route: "/admin/errorlog" },
            { name: '', display: '', route: "/admin/overview",img:"bi bi-house-door"},
            { name: '', display: '', route: "/admin/thing",img:"bi bi-exclamation-circle"},
        ];
    
        const selectItem = (item) => {
            selectedItem.value = item.name; // 
            // console.log(item)
            router.push(item.route); // 
        };
    
        return {
            selectedItem,
            menuItems,
            selectItem,
        };
    }
  };
  </script>
  
  <style scoped>
  .container{
    height: 100%;
  }
  /* ul{
    background-color: "#001529";
  } */
  .list-group {
    border-radius: 8px;
    flex: 1; /*  */
    overflow: hidden;
    margin: 0 auto;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .list-group-item {
    border: none;
    padding: 15px;
    text-align: center;
    transition: background-color 0.3s ease, color 0.3s ease;
    cursor: pointer; /*  */
  }
  
  .list-group-item.active {
    background-color: #007bff;
    color: white;
  }
  
  .list-group-item:not(.active):hover {
    background-color: #f8f9fa;
    color: #343a40;
  }

  </style>